body {
  --hover-color: #00acdc;
  margin: 0;
}

.header {
  width: 100%;
  position: absolute;
  top: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  position: relative;
}

.header__image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.header__nav {
  display: flex;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__item {
  text-align: center;
  align-items: center;
  height: 60px;
  display: flex;
  position: relative;
}

.header__item:not(:first-child) {
  margin-left: 10px;
}

.header__item:not(:last-child) {
  margin-right: 10px;
}

.header__link {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.header__link:hover, .header__item.is-active > .header__link {
  color: var(--hover-color);
}

.header__item.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 100px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.header__link[data-qa="hover"]:hover {
  color: var(--hover-color);
}
